import { motion } from 'framer-motion';
import { Ticket } from 'lucide-react';

const EmptyState = () => (
  <motion.div
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    className="text-center py-12 bg-white rounded-lg shadow-sm mt-6"
  >
    <Ticket className="w-12 h-12 text-gray-400 mx-auto mb-4" />
    <p className="text-gray-500">هیچ تیکتی با فیلترهای انتخاب شده یافت نشد.</p>
  </motion.div>
);

export default EmptyState;