import { motion } from "framer-motion";
import { Eye, Trash2 } from "lucide-react";

const TicketRow = ({ ticket, handleStatusChange, handleDelete, navigate }) => {

  return (
    <motion.tr
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      transition={{ duration: 0.3 }}
    >
      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-200">
        {ticket.id}
      </td>
      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
        {ticket.customer_name}
      </td>
      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
        {ticket.subject}
      </td>
      <td className="px-6 py-4 whitespace-nowrap">
        <select
          value={ticket.status}
          onChange={(e) => handleStatusChange(ticket.id, e.target.value)}
          className="appearance-none px-4 py-2 pr-8 rounded-lg bg-gray-700 border border-gray-800 text-gray-300 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 relative"
          style={{
            backgroundImage: `url("data:image/svg+xml;utf8,<svg fill='gray' height='18' viewBox='0 0 24 24' width='18' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/></svg>")`,
            backgroundRepeat: "no-repeat",
            backgroundPosition: "right 0.75rem center",
            backgroundSize: "1rem",
          }}
        >
          <option value="open">باز</option>
          <option value="pending">در انتظار</option>
          <option value="closed">بسته</option>
        </select>
      </td>
      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
        {new Date(ticket.created_at).toLocaleDateString("fa-IR")}
      </td>
      <td className="px-6 py-4 whitespace-nowrap text-sm">
        <div className="flex gap-2">
          <button
            className="text-blue-500 hover:text-blue-700"
            onClick={() => navigate(`/dashboard/tickets/${ticket.id}`)}
          >
            <Eye className="w-5 h-5" />
          </button>
          <button
            className="text-red-500 hover:text-red-700"
            onClick={() => handleDelete(ticket.id)}
          >
            <Trash2 className="w-5 h-5" />
          </button>
        </div>
      </td>
    </motion.tr>
  );
};

export default TicketRow;
