import React, { useState, useEffect } from "react";
import {
  Ticket,
  Search,
  Filter,
  ChevronDown,
  Eye,
  Edit,
  Trash2,
} from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { useNavigate } from "react-router-dom";
import TicketToolbar from "./TicketToolbar";
import TicketRow from "./TicketRow";
import EmptyState from "./EmptyState";
import { Helmet } from "react-helmet";
import toast from "react-hot-toast";

const ManageTickets = () => {
  const navigate = useNavigate();
  const [tickets, setTickets] = useState([]);
  const [filteredTickets, setFilteredTickets] = useState(tickets);
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState("all");
  const [isFilterOpen, setIsFilterOpen] = useState(false);
  const [sortedTickets, setSortedTickets] = useState([]);

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/tickets/"
      : "https://allmaxsport.com/api/tickets/";

  useEffect(() => {
    async function loadTickets() {
      const resposne = await fetch(API_URL);
      const data = await resposne.json();
      setTickets(data);
    }

    loadTickets();
  }, []);

  useEffect(() => {
    let result = tickets;

    if (searchTerm) {
      result = result.filter(
        (ticket) =>
          ticket.user.toLowerCase().includes(searchTerm.toLowerCase()) ||
          ticket.subject.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (statusFilter !== "all") {
      result = result.filter((ticket) => ticket.status === statusFilter);
    }

    setFilteredTickets(result);
  }, [searchTerm, statusFilter, tickets]);

  const handleStatusChange = (ticketId, newStatus) => {
    setTickets(
      tickets.map((ticket) =>
        ticket.id === ticketId
          ? {
              ...ticket,
              status: newStatus,
              updated_at: new Date().toISOString(),
            }
          : ticket
      )
    );
  };

  const handleDelete = (ticketId) => {
    setTickets(tickets.filter((ticket) => ticket.id !== ticketId));
    try {
      fetch(`${API_URL}?id=${ticketId}`, {
        method: "DELETE",
      })
        .then((res) => res.json())
      toast.success("تیکت مورد نظر با موفقیت پاک شد");
    } catch (e) {
    }
  };

  // Helper function to format date in Farsi
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return new Intl.DateTimeFormat("fa-IR", {
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    }).format(date);
  };

  // Status badge component with appropriate colors for dark theme
  const StatusBadge = ({ status }) => {
    const statusMap = {
      open: { text: "باز", bg: "bg-emerald-900", text: "text-emerald-200" },
      pending: {
        text: "در حال بررسی",
        bg: "bg-amber-900",
        text: "text-amber-200",
      },
      closed: { text: "بسته شده", bg: "bg-gray-800", text: "text-gray-300" },
    };

    const { text, bg } = statusMap[status];
    return (
      <span
        className={`px-2 py-1 rounded-full text-xs font-medium ${bg} ${text}`}
      >
        {text}
      </span>
    );
  };

  useEffect(() => {
    const sortedTickets = filteredTickets.sort(
      (a, b) => a.created_at - b.created_at
    );
    setSortedTickets(sortedTickets);
  }, [filteredTickets]);

  return (
    <>
      <Helmet>
        <title>المکس | تیکت‌ها</title>
      </Helmet>
      <div className="p-3 sm:p-6 max-w-7xl mx-auto">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-4 sm:mb-8 gap-3">
          <motion.h1
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-xl sm:text-2xl font-bold text-gray-100 flex items-center"
          >
            <Ticket className="w-5 h-5 sm:w-6 sm:h-6 ml-2" />
            مدیریت تیکت‌ها
          </motion.h1>

          {/* Mobile Search and Filter */}
          <div className="flex w-full sm:hidden gap-2">
            <div className="relative flex-grow">
              <input
                type="text"
                placeholder="جستجو..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full px-4 py-2 bg-zinc-800 text-white border border-gray-700 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 pr-10"
              />
              <Search className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
            </div>
            <button
              onClick={() => setIsFilterOpen(!isFilterOpen)}
              className="flex items-center justify-center p-2 bg-zinc-800 border border-gray-700 rounded-lg hover:bg-zinc-700 transition-colors"
            >
              <Filter className="h-5 w-5 text-gray-200" />
            </button>
          </div>

          {/* Mobile Filter Dropdown */}
          <AnimatePresence>
            {isFilterOpen && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: "auto" }}
                exit={{ opacity: 0, height: 0 }}
                className="w-full sm:hidden bg-zinc-800 rounded-lg p-3 border border-gray-700 overflow-hidden"
              >
                <div className="flex flex-col gap-2">
                  <span className="text-gray-300 text-sm">
                    فیلتر بر اساس وضعیت:
                  </span>
                  <div className="flex flex-wrap gap-2">
                    {["all", "open", "pending", "closed"].map((status) => (
                      <button
                        key={status}
                        onClick={() => setStatusFilter(status)}
                        className={`px-3 py-1.5 rounded-md text-sm ${
                          statusFilter === status
                            ? "bg-blue-600 text-white"
                            : "bg-zinc-700 text-gray-300 hover:bg-zinc-600"
                        }`}
                      >
                        {status === "all"
                          ? "همه"
                          : status === "open"
                          ? "باز"
                          : status === "pending"
                          ? "در حال بررسی"
                          : "بسته شده"}
                      </button>
                    ))}
                  </div>
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </div>

        {/* Desktop Toolbar */}
        <div className="hidden sm:block">
          <TicketToolbar
            searchTerm={searchTerm}
            setSearchTerm={setSearchTerm}
            statusFilter={statusFilter}
            setStatusFilter={setStatusFilter}
          />
        </div>

        {/* Desktop Table View */}
        <div className="hidden sm:block bg-zinc-800 rounded-lg shadow-lg overflow-hidden border border-gray-700">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-zinc-900">
                <tr>
                  <th className="px-6 py-3 text-right text-sm font-medium text-gray-300">
                    شناسه
                  </th>
                  <th className="px-6 py-3 text-right text-sm font-medium text-gray-300">
                    کاربر
                  </th>
                  <th className="px-6 py-3 text-right text-sm font-medium text-gray-300">
                    موضوع
                  </th>
                  <th className="px-6 py-3 text-right text-sm font-medium text-gray-300">
                    وضعیت
                  </th>
                  <th className="px-6 py-3 text-right text-sm font-medium text-gray-300">
                    ایجاد شده
                  </th>
                  <th className="px-6 py-3 text-right text-sm font-medium text-gray-300">
                    عملیات
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-700">
                <AnimatePresence>
                  {sortedTickets?.map((ticket) => (
                    <TicketRow
                      key={ticket.id}
                      ticket={ticket}
                      handleStatusChange={handleStatusChange}
                      handleDelete={handleDelete}
                      navigate={navigate}
                    />
                  ))}
                </AnimatePresence>
              </tbody>
            </table>
          </div>
        </div>

        {/* Mobile Card View */}
        <div className="sm:hidden space-y-4 mt-3">
          <AnimatePresence>
            {filteredTickets.map((ticket) => (
              <motion.div
                key={ticket.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, scale: 0.9 }}
                transition={{ type: "spring", stiffness: 300, damping: 30 }}
                className="bg-zinc-800 rounded-lg shadow p-4 border border-gray-700"
              >
                <div className="flex justify-between items-start mb-3">
                  <span className="text-gray-400 text-xs">#{ticket?.id}</span>
                  <StatusBadge status={ticket?.status} />
                </div>

                <h3 className="text-gray-100 font-bold mb-2">
                  {ticket?.subject}
                </h3>

                <div className="flex justify-between items-center mb-3">
                  <span className="text-gray-300 text-sm">{ticket?.user}</span>
                  <span className="text-gray-400 text-xs">
                    {formatDate(ticket?.created_at)}
                  </span>
                </div>

                <div className="border-t border-gray-700 pt-3 flex justify-between">
                  <div className="flex space-x-2 space-x-reverse">
                    <button
                      onClick={() => navigate(`/tickets/${ticket.id}`)}
                      className="p-1.5 bg-blue-900 text-blue-200 rounded-md hover:bg-blue-800 transition-colors"
                      title="مشاهده"
                    >
                      <Eye size={16} />
                    </button>

                    <button
                      onClick={() => navigate(`/tickets/edit/${ticket.id}`)}
                      className="p-1.5 bg-amber-900 text-amber-200 rounded-md hover:bg-amber-800 transition-colors"
                      title="ویرایش"
                    >
                      <Edit size={16} />
                    </button>

                    <button
                      onClick={() => handleDelete(ticket.id)}
                      className="p-1.5 bg-red-900 text-red-200 rounded-md hover:bg-red-800 transition-colors"
                      title="حذف"
                      type="button"
                    >
                      <Trash2 size={16} />
                    </button>
                  </div>

                  <select
                    value={ticket.status}
                    onChange={(e) =>
                      handleStatusChange(ticket.id, e.target.value)
                    }
                    className="bg-zinc-700 text-gray-200 text-sm rounded-md border border-gray-600 py-1 px-2 focus:outline-none focus:ring-1 focus:ring-blue-500"
                  >
                    <option value="open">باز</option>
                    <option value="pending">در حال بررسی</option>
                    <option value="closed">بسته شده</option>
                  </select>
                </div>
              </motion.div>
            ))}
          </AnimatePresence>
        </div>

        {filteredTickets.length === 0 && <EmptyState />}

        {/* Pagination (Optional) */}
        {filteredTickets.length > 0 && (
          <div className="mt-6 flex justify-center">
            <nav className="flex items-center space-x-2 space-x-reverse">
              <button className="px-3 py-1 rounded bg-zinc-800 text-gray-300 hover:bg-zinc-700 border border-gray-700">
                قبلی
              </button>
              <button className="px-3 py-1 rounded bg-blue-600 text-white text-center">
                1
              </button>
              <button className="px-3 py-1 rounded bg-zinc-800 text-gray-300 hover:bg-zinc-700 border border-gray-700">
                بعدی
              </button>
            </nav>
          </div>
        )}
      </div>
    </>
  );
};

export default ManageTickets;
