import { Search } from "lucide-react";

const TicketToolbar = ({
  searchTerm,
  setSearchTerm,
  statusFilter,
  setStatusFilter,
}) => (
  <div className="bg-zinc-800 rounded-lg shadow-sm p-6 mb-6">
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <div className="relative">
        <Search className="w-5 h-5 absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
        <input
          type="text"
          placeholder="جستجو بر اساس کاربر یا موضوع..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full p-3 pr-10 border bg-zinc-700 text-gray-200 border-gray-800 placeholder:text-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
      </div>

      <select
        value={statusFilter}
        onChange={(e) => setStatusFilter(e.target.value)}
        className="appearance-none px-4 py-2 pr-8 rounded-lg bg-zinc-700 border border-gray-800 text-gray-200 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 relative"
        style={{
          backgroundImage: `url("data:image/svg+xml;utf8,<svg fill='gray' height='18' viewBox='0 0 24 24' width='18' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/></svg>")`,
          backgroundRepeat: "no-repeat",
          backgroundPosition: "right 0.75rem center",
          backgroundSize: "1rem",
        }}
      >
        <option value="all">همه وضعیت‌ها</option>
        <option value="open">باز</option>
        <option value="pending">در انتظار</option>
        <option value="closed">بسته</option>
      </select>
    </div>
  </div>
);

export default TicketToolbar;
